import { html, LitElement, css } from "lit";

export class PronosticoHour extends LitElement {
  static get styles() {
    return css`
      :host {
        width: 100%;
        text-align: center;
      }

      .table-responsive {
        display: flex;
        flex-direction: column;
        margin:0 15px;
        
      }
      .title-city {
        text-align: center;
        color:#033524;
      }

      .contain-info-time {
        display: flex;
        width:100%;
        overflow:scroll;
        scrollbar-color: yellow #800080;
    scrollbar-width: 10px;
      }

      .astro{
        margin:10px 20px;
        background-color:#1bca90;
        color:#033524;
      }

      .astro tbody{
        padding:5px;
      }

      .icon {
        height: 73.48px;
      }
      .empty {
        height: 46px;
        
      }

      .container-table{
        width:100%;
      }
      table{
        border:none;
      }
    
      .table-row{
        color:#042c1f;
        
      }
      .default thead tr th{
        font-size:0.8rem;
        padding:6px;
        width:150px;
      }

      .contain-info-time tr th{
      padding:0 10px;
      }

      @media (min-width: 812px) {
        .container-table{
          min-width:768px;
        }
      }
    `;
  }

  static get properties() {
    return {
      weather: { type: Object },
      city: { type: String },
      page: { type: Number },
    };
  }

  render() {
    return html`
      <h3 class="title-city">Today Weather in ${this.city}</h3>
      <div class="overzise">
      ${this.weather &&
      this.weather.forecast.forecastday.map((days) => {
        return html`
          <div id="${days.date_epoch}" class="container-table">
            <div class="table-responsive">
              <table class="astro">
                <tbody>
                  <th>
                    Sunrise ${days.astro.sunrise} <br />Sunset
                    ${days.astro.sunset}
                  </th>
                  <th>
                    Monrise ${days.astro.moonrise} <br />Moonset
                    ${days.astro.moonset}
                  </th>
                  <th>Max: ${days.day.maxtemp_c}</th>
                  <th>Min: ${days.day.mintemp_c}</th>
                  <th>Avg: ${days.day.avgvis_km}</th>
                  <th>Precip: ${days.day.totalprecip_mm}</th>
                  <th>Max Wind: ${days.day.maxwind_mph}</th>
                </tbody>
              </table>
              <div class="contain-info-time">
                <table border="1" class="table-row">
                  <tr class="empty">
                    <th>&nbsp;</th>
                  </tr>
                  <tr class="icon">
                    <td>Icon</td>
                  </tr>
                  <tr>
                    <td>Temp</td>
                  </tr>
                  <tr>
                    <td>Wind</td>
                  </tr>
                  <tr>
                    <td>Precip</td>
                  </tr>
                  <tr>
                    <td>Cloud</td>
                  </tr>
                  <tr>
                    <td>Humidity</td>
                  </tr>
                  <tr>
                    <td>Pressure</td>
                  </tr>
                </table>
                ${days.hour.map((day) => {
                  return html`
                    <table class="default" border="1">
                      <thead>
                        <tr>
                          <th>${day.time}</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <th><img src="${day.condition.icon}" alt="" /></th>
                        </tr>

                        <tr>
                          <th>${day.temp_c}c</th>
                        </tr>

                        <tr>
                          <th>${day.wind_mph}kmph</th>
                        </tr>

                        <tr>
                          <th>${day.precip_mm}mm</th>
                        </tr>

                        <tr>
                          <th>${day.cloud}</th>
                        </tr>

                        <tr>
                          <th>${day.humidity}</th>
                        </tr>

                        <tr>
                          <th>${day.pressure_mb}</th>
                        </tr>
                      </tbody>
                    </table>
                  `;
                })}
              </div>
            </div>
          </div>
        `;
      })}
      </div>
    `;
  }

}

customElements.define("pronost-ui", PronosticoHour);
